body {
  font-family: 'PT Serif', Georgia, serif;
  font-weight: 400;
  color: #2a2a2a;
  margin: 0;
  line-height: 1.65;
}
section {
  padding: 30px 15px;
}
p {
  font-size: 18px;
  letter-spacing: -0.004em;
  line-height: 1.58;
}
.intro p:first-letter {
  font-family: 'PT Sans', Arial, sans-serif;
  font-size: 56px;
  font-weight: 700;
  float: left;
  line-height: 30px;
  padding-right: 4px;
  padding-top: 15px;
  padding-bottom: 0;
  margin: 0;
}
p strong {
  font-size: inherit;
  line-height: inherit;
  font-weight: 700;
}
p.dinkus {
  text-align: center;
}
h1 {
  font-family: 'PT Sans', Arial, sans-serif;
  font-size: 125px;
  letter-spacing: 2px;
  font-weight: 700;
  margin: 0 0 20px;
  text-align: center;
}
h2 {
  font-family: 'PT Sans', Arial, sans-serif;
  font-size: 24px;
  margin: 0 0 20px;
  line-height: 1.4;
}
h1,
a {
  color: #303ab2;
}
a {
  text-decoration: none;
  border-bottom: 1px dotted #4a4a4a;
}
a:visited {
  color: pink;
}
img {
  width: 100%;
  height: auto;
}
footer {
  border-top: 5px solid #303ab2;
  color: grey;
  font-family: "PT Sans", Arial, sans-serif;
  padding: 25px;
  text-align: center;
}
section:nth-child(even) {
  background-color: #f8f8f8;
}
section.currently {
  color: white;
  background-color: #303ab2;
}
section.skills {
  color: white;
  background-color: #2a2a2a;
}
@media screen and (min-width: 768px) {
  header {
    position: fixed;
    font-family: 'PT Sans', Arial, sans-serif;
    color: black;
    font-weight: 700;
    letter-spacing: 1;
    top: 0;
    left: 0;
    padding: 10px 20px;
    text-transform: uppercase;
    background: rgba(255, 255, 255, 0.75);
  }
  section {
    padding: 0;
    display: flex;
    height: 100%;
    align-items: center;
  }
  .content {
    margin: 0 auto;
    max-width: 728px;
  }
  p {
    font-size: 21px;
  }
  .intro p:first-letter {
    font-size: 70px;
    padding-right: 8px;
    padding-top: 22px;
  }
}
